




















.navbar nav{
    padding: 0 30px;
}

.index{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  margin-top:40px;
}
.index h2{
  font-size: 1.5em;
  text-align: center;
  margin-top: 0;
}
.index .ingredients{
  margin: 30px auto ;
}
.index .ingredients li{
  display: inline-block;
}
.index .delete{
  position: absolute;
  top:4px;
  right:4px;
  cursor:pointer;
  color:#aaa;
  font-size: 1.4em;
}
.index .edit-btn{
  position: absolute;
  right:6px;
  bottom: 6px;
  cursor:pointer;
}

.addrecipe{
    margin-top: 50px;
    padding: 20px;
    max-width: 500px;
}
.addrecipe h2{
    font-size: 2em;
    margin: 20px auto;
}
.addrecipe .field{
    margin: 20px auto;
    position: relative;
}
.addrecipe .delete{
    position: absolute;
    right: 0;
    bottom: 16px;
    color: #aaa;
    font-size: 1.3em;
    cursor: pointer;
}

